
.showRouter-enter-active {
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
}
.showRouter-leave-active {
    -webkit-transition: all 0 ease;
    transition: all 0 ease;
}
.showRouter-enter, .showRouter-leave-active {
    -webkit-transform: translateX(-7.5rem);
            transform: translateX(-7.5rem);
    opacity: 0;
}
.music-list {
    padding-top: 4.15rem;
    padding-left: 0.2rem;
    padding-right: 0.2rem;
    margin-bottom: 3.5rem;
    -webkit-box-flex:8;
        -ms-flex:8;
            flex:8;
    overflow: auto;
}
.music-item + .music-item {
    border-top: 0.05rem solid rgba(0, 0, 0, .1);
}
.music-item {
   
    padding: 0.2rem 0.3rem 0 0.3rem;
    position: relative;
    margin-bottom: 0.2rem;
    border-radius: 0.25rem;
    cursor: pointer;
    border: none;
}
.music-img {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 0.25rem;
}
span.music-name {
    display: inline-block;
    width: 65%;
    vertical-align: top;
    margin-top: 0.75rem;
    margin-left: 0.5rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    cursor: pointer;

    padding-bottom: 1rem;
}
span.del-icon {
    display: inline-block;
    position: absolute;
    right: 0.5rem;
    top: 1rem;
    width: 1rem;
    height: 1rem;
    background: url('./del.svg');
    background-size: contain;
    cursor: pointer;
}
.tips {
  text-align: center;
  margin: 0.6rem auto;
  width: 10rem;
  font-size: 80%;
  color: gray;
}
  



